<script>
import axios from "axios";
import { eventBus } from "../../main";

  export default {
    name: 'OtherUserProfileHeader',
    data() {
        return {
          errors: [],
          following : []
        }
    },

    props: {
      user: {
        type: Object,
        required: true
      }
    },
    created: function() {
      this.getFollowing();
    },
    methods: {

      follow (userId){
        const bodyContent = { name: userId};
        axios
          .post("/api/follows/", bodyContent)
          .then(() => {
            // handle success
            this.following.push(userId);
            eventBus.$emit('follow-success', userId);
          })
      },

      isFollowing (userId) {
        
        for (const followId of this.following) {

          if (userId === followId) {
            return true
          }
        }

        return false
      },

      getFollowing () {
        const bodyContent = {};
        axios
          .get("/api/follows/following", bodyContent)
          .then((res) => {
            this.following = res.data.following.map(u => u.followedID);
          });
          
      },
      unFollow (userId){
        const bodyContent = { name: userId};
        axios
          .put("/api/follows/", bodyContent)
          .then(() => {
            // handle success
            eventBus.$emit('unfollow-success', userId);
            var index = this.following.indexOf(userId);
            if (index !== -1) {
              this.following.splice(index, 1);
            }
          })
      },

      // unFollow (userId) {
      //   const token = localStorage.getItem('tweetr-token')

