      //       this.authUser.following = this.authUser.following.filter(u => {
      //         return u.id !== userId
      //       })
      //     })
      // },

    clearMessages: function() {
      setInterval(() => {
        this.errors = [];
      }, 5000);
    }
    }
  }

</script>