<template>
  <div>

   <OtherUserProfileHeader
      :user="user"
    />

    <div class="ui stackable grid container">
      <div class="four wide column">
        <OtherUserProfileSidebarDetails :user="user"/>
      </div>
      <div class="eight wide column">
        <div class="ui segment">
          <h2 class="ui medium dividing header">Freets</h2>

      </div>

      </div>
     <!--  <div class="four wide column">
        <WhoToFollow v-if="isLoggedIn"/>
      </div> -->
    </div>
    <div v-if='messages.length' class="success-message" style="text-align:center;">
      <div v-for='message in messages' v-bind:key='message.id'>{{ message }}</div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../../main";

import OtherUserProfileHeader from './OtherUserProfileHeader'
import OtherUserProfileSidebarDetails from './OtherUserProfileSidebarDetails'
// import WhoToFollow from '@/components/User/WhoToFollow'
// import Tweets from '@/components/Tweet/Tweets'

  export default {
    name: 'OtherUserProfile',
    components: {
      OtherUserProfileSidebarDetails,
      OtherUserProfileHeader,
      // WhoToFollow,
      // Tweets
    },
    data () {
      return {
        user: {},
        messages: []
        //authUser: '',
        //tweets: []
      }
    },
    // beforeRouteUpdate (to, from, next) {
    //   this.fetchUser(to.params.username);

    //   next();
    // },
    computed: {

    },
    created () {
      this.fetchUser(this.$route.params.username);

      eventBus.$on("follow-success", (username) => {
      this.messages.push(`You have successfuly followed this user ${username}!`);
      this.clearMessages();
    });

      eventBus.$on("unfollow-success", (username) => {
      this.messages.push(`You have successfuly unfollowed this user ${username}!`);
      this.clearMessages();
    });

    },
    methods: {
      fetchUser (username) {
        axios.get(`/api/users/${username}`).then(response => {
          this.user = response.data.user;
          // this.tweets = response.data.data.tweets.reverse()
        })
      },

      clearMessages: function() {
      setInterval(() => {
        this.messages = [];
      }, 5000);
      
      },
      
    }
  }
</script>